$("._toggle-profile-menu").click(function () {
    $(".m-page").toggleClass("_open-profile-menu")
    $(".m-page").toggleClass("fixed-body")
    $(".menu-group").removeClass("_open")
})

$(".menu-group__show").click(function () {
    if(!$(this).parents(".menu-group").hasClass("_open")){
        $(".menu-group").removeClass("_open")
        $(this).parents(".menu-group").addClass("_open")
    } else{
        $(".menu-group").removeClass("_open")
    }
})

$(".address-card ._edit").click(function () {
    let parent = $(this).parents(".address-card")
    if(!$(parent).hasClass("_active")){
        $(parent).addClass("_active")
        $(parent).find(".address-card__field input").prop("disabled",false)
    } else{
        $(parent).removeClass("_active")
        $(parent).find(".address-card__field input").prop("disabled",true)
    }
})

$(".user-addresses__add").click(function () {
    let parent = $(this).parents(".user-addresses")
    $(parent).toggleClass("_new-address")
    $(".user-addresses__new").slideToggle()
})

$(".organization-add__field input").on("input",function () {
    if($(this).val().length > 3){
        $(this).parents(".organization-add__field").addClass("_show")
    }
})

$(".order-card__more").click(function () {
    $(this).parents(".order-card").addClass("_show-all")
    $(this).hide()
})

$("input[name='appealType']").change(function () {
    $(".new-appeal__hidden").slideDown()
})

$(".qa-card__show").click(function () {
    $(this).parents(".qa-card").toggleClass("_active")
    $(this).siblings(".qa-card__hidden").slideToggle()
})
