setUserTypeFields()
$("input[name='userType']").change(function () {
    setUserTypeFields()
})

function setUserTypeFields() {
    let checkedVal = $("input[name='userType']:checked").val()

    $(".user-type").removeClass("_active")
    $(`.user-type._${checkedVal}`).addClass("_active")
}

setDeliveryTypeFields()
$("input[name='deliveryType']").change(function () {
    setDeliveryTypeFields()
})

function setDeliveryTypeFields() {
    let checkedVal = $("input[name='deliveryType']:checked").val()

    $(".delivery-way").removeClass("_active")
    $(`.delivery-way._${checkedVal}`).addClass("_active")
}
