$("[data-copy]").click(function () {
    let text = $(this).attr("data-copy")
    let $tmp = $("<textarea>");
    $("body").append($tmp);
    $tmp.val(text).select();
    document.execCommand("copy");
    $tmp.remove();
})

$("._show-more-reviews").click(function () {
    $(".catalog-detail").find("[data-tab='_tab3']").click()
})
