$("._toggle-mob-menu").click(function () {
    $("body").toggleClass("_open-mob-menu")
    $("body").toggleClass("fixed-body")
})


$("._search-field").on("input",function () {
    if($(this).val().length > 3){
        $(this).parents(".m-search").addClass("_show")
    }
})

$(document).mouseup( function(e){
    var div = $( ".tabs-select,.m-search,.tablet-menu,.organization-add__field" );
    if ( !div.is(e.target)
        && div.has(e.target).length === 0 ) {
        if($(div).hasClass("_show")){
            $(div).removeClass("_show")
        }
    }
});

$(".header-search__close").click(function () {
    $(".m-search").removeClass("_show")
})


$(".nav__link").each(function (i,el) {
    if(i > 4){
        $(".tablet-menu__links").append($(el).clone())
        $(el).addClass("_hide-tablet")
    }
})


$(".tablet-menu__show").click(function () {
    $(".tablet-menu").toggleClass("_show")
})
