$(".cart-btn").click(function () {
    $(this).toggleClass("_added")

    if(!$(this).hasClass("_added")){
        return
    }

    if(!$(this).attr("data-product")){
        return
    }

    let product = JSON.parse($(this).attr("data-product"))

    let productHtml = `
        <div class="product-alert">
            <div class="product-alert__img">
                <img src="${product.img}" alt="">
            </div>
            <div class="product-alert__content">
                <div class="product-alert__message">
                    успешно добавлено в корзину
                </div>
                <div class="product-alert__title">
                    ${product.title}
                </div>
                <div class="product-prices">
                    <div class="product-prices__item">
                        <div class="product-prices__old">
                            ${product.price2}
                        </div>
                    </div>
                    <div class="product-prices__item">
                        <div class="product-prices__current">
                            ${product.price}
                        </div>
                    </div>
                </div>
            </div>
            <button class="product-alert__close">
                <svg width="10" height="10" viewBox="0 0 10 10" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path opacity="0.5" d="M9.66937 0.226864C9.52925 0.0864247 9.33901 0.0075 9.14062 0.0075C8.94224 0.0075 8.752 0.0864247 8.61188 0.226864L4.94438 3.88686L1.27688 0.219364C1.13675 0.0789247 0.946513 0 0.748125 0C0.549737 0 0.359499 0.0789247 0.219375 0.219364C-0.073125 0.511864 -0.073125 0.984364 0.219375 1.27686L3.88687 4.94436L0.219375 8.61186C-0.073125 8.90436 -0.073125 9.37686 0.219375 9.66936C0.511875 9.96186 0.984375 9.96186 1.27688 9.66936L4.94438 6.00186L8.61188 9.66936C8.90438 9.96186 9.37687 9.96186 9.66937 9.66936C9.96187 9.37686 9.96187 8.90436 9.66937 8.61186L6.00187 4.94436L9.66937 1.27686C9.95438 0.991864 9.95438 0.511864 9.66937 0.226864Z" fill="#4A4A4A"/>
                </svg>
            </button>
        </div>
        `

    $(".m-page").append(productHtml)
    setTimeout(()=>{
        $(".product-alert").addClass("_active")
    },10)

    $(".product-alert__close").click(function () {
        closeAlert(".product-alert")
    })

    setTimeout(()=>{
        closeAlert(".product-alert")
    },10000)
})

function closeAlert(el) {
    $(el).removeClass("_active")
    setTimeout(()=>{
        $(el).remove()
    },300)
}

$(".footer-nav__show").click(function () {
    if(window.innerWidth<=576){
        $(this).parents(".footer-nav").toggleClass("_active")
        $(this).siblings(".footer-nav__hidden").slideToggle()
    }
})

function cartCalc() {
    $('.cartcalc .ccalc-minus').click(function(e) {
        let a = $(this).closest('.cartcalc').find('input').val();
        if (a > 1) {
            let b = +a - 1;
            $(this).closest('.cartcalc').find('input').val(b);
            $(this).closest('.cartcalc').find('input').trigger('change');
        } else {
            $(this).closest('.cartcalc').find('input').val(a);
        }
        e.stopImmediatePropagation()
    });

    $('.cartcalc .ccalc-plus').click(function(e) {
        let a = $(this).closest('.cartcalc').find('input').val();
        let b = +a + 1;
        $(this).closest('.cartcalc').find('input').val(b);
        $(this).closest('.cartcalc').find('input').trigger('change');
        e.stopImmediatePropagation()
    });

}

cartCalc()

$(".pass-toggle").click(function () {
    if($(this).hasClass("_active")){
        $(this).removeClass("_active")
        $(this).siblings("input").attr("type","password")
    } else{
        $(this).addClass("_active")
        $(this).siblings("input").attr("type","text")
    }
})

let breadcrumbsItemsCount = $(".breadcrumbs__item").length

if(breadcrumbsItemsCount > 2){
    $(".breadcrumbs__item:nth-child(2)").addClass("_dots-mob")
}
if(breadcrumbsItemsCount > 3){
    $(".breadcrumbs__item:nth-child(3)").addClass("_hide-mob")
}

$("._tab").click(function() {
    let parentBlock = $(this).parents("._tabs-parent")
    let tabId = $(this).attr("data-tab")
    $(parentBlock).find("._tab").removeClass("_active")
    $(this).addClass("_active")
    $(parentBlock).find(".tab-content").removeClass("_active")
    $(parentBlock).find(`.${tabId}`).addClass("_active")

    if($(parentBlock).hasClass("_tabs-selectable")){
        setTabSelect(parentBlock)
        $(parentBlock).find(".tabs-select").removeClass("_show")
    }
})

$("._tabs-selectable").each(function (i,el) {
    setTabSelect(el)
})

function setTabSelect(el){
    $(el).find(".tabs-select__current").html($(el).find("._tab._active").html())

}

$(".tabs-select__show").click(function () {
    $(this).parents(".tabs-select").toggleClass("_show")
})


$(".rating-input__item").click(function() {
    let ratingItem = parseInt($(this).attr("data-rating-value"))
    $(this).parent(".rating-input").attr("data-total-rating", ratingItem)
    $(this).siblings(".rating-input__field").val(ratingItem)
})
